import { Input, OnInit } from '@angular/core';

import { ThfPageBlockedUserReason } from './enums/thf-page-blocked-user-reason.enum';
import { ThfPageBlockedUserReasonParams } from './interfaces/thf-page-blocked-user-reason-params.interface';

const ThfPageBlockedUserReasonDefault: ThfPageBlockedUserReason = ThfPageBlockedUserReason.None;
const ThfPageBlockedUserParamsDefault: ThfPageBlockedUserReasonParams = { attempts: 5, days: 90, hours: 24 };

/**
 * @description
 *
 * O componente `thf-page-blocked-user`  utilizado como template para tela de bloqueio de usurio.
 *  possvel definir entre trs tipos de telas para alertar o usurio sobre um eventual bloqueio de login.
 *
 * Cada modelo de bloqueio possui uma imagem e texto adequados  situao.
 * Os textos das telas so pr-definidos e imutveis, porm,
 *  possvel estipular parmetros como dias, horas e tentativas de acesso esgotadas.
 *
 * Por fim, h propriedades para adio de telefone e/ou email para contato e tambm a definio para a url de retorno.
 *
 * Para que as imagens sejam exibidas corretamente,  necessrio incluir o caminho delas ao projeto. Para isso, edite
 * o *assets* no arquivo **angular.json** da aplicao na seguinte ordem:
 * ```
 *   "assets": [
 *     "src/assets",
 *     "src/favicon.ico",
 *     {
 *       "glob": "**\/*",
 *       "input": "node_modules/@totvs/thf-theme/images",
 *       "output": "assets/images"
 *     }
 *   ]
 * ```
 *
 * _______________
 *
 * #### Praticidade
 * O `thf-page-blocked-user`, assim como suas propriedades, pode tambm ser transmitido diretamente pelas configuraos de rota e,
 * desta maneira, dispensa-se qualquer meno e/ou importao no restante da aplicao. O exemplo abaixo exemplifica
 * a forma dinmica com a qual o template pode ser gerado se navegasse para uma rota denominada como `/access-denied`:
 *
 *
 * ```
 *   import { ThfPageBlockedUserComponent, ThfPageBlockedUserReason } from '@totvs/thf-templates';
 *
 *   ...
 *   const routes: Routes = [
 *     {
 *       path: 'access-denied', component: ThfPageBlockedUserComponent, data: {
 *         contactEmail: 'dev.totvs@totvs.com',
 *         contactPhone: '0800 1234 000',
 *         reason: ThfPageBlockedUserReason.ExpiredPassword,
 *         urlBack: '/home'
 *       }
 *     }
 *     ...
 *   ];
 *
 *   @NgModule({
 *     imports: [RouterModule.forRoot(routes)],
 *     exports: [RouterModule]
 *   })
 *   export class AppRoutingModule { }
 * ```
 *
 * >  essencial que siga a nomenclatura dos atributos exemplificados acima para sua efetiva funcionalidade.
 *
 */
export class ThfPageBlockedUserBaseComponent {

  private _params: ThfPageBlockedUserReasonParams = {...ThfPageBlockedUserParamsDefault};
  private _reason: ThfPageBlockedUserReason = ThfPageBlockedUserReason.None;
  private _urlBack: string = '/';

  /**
   * @optional
   *
   * @description
   *
   * Valor para o email de contato que deve ser exibido. A ao est de acordo com o protocolo MAILTO e  possvel definir
   * tanto rotas internas quanto externas.
   */
  @Input('t-contact-email') contactEmail: string;

  /**
   * @optional
   *
   * @description
   *
   * Valor para o telefone de contato que deve ser exibido. A ao est de acordo com o protocolo TEL.
   *
   * > A propriedade no contem tratamento de mscara, fica a critrio do desenvolvedor defini-la.
   */
  @Input('t-contact-phone') contactPhone: string;

  /**
   * @optional
   *
   * @description
   *
   * Designao de valores usados para a customizao da mensagem de bloqueio.
   * Confira abaixo os valores pr-definidos.
   *
   * ```
   *  const customLiterals: ThfPageBlockedUserReasonParams = {
   *    attempts: 5,
   *    days: 90,
   *    hours: 24
   *  };
   * ```
   *
   * > Salientamos a importncia e ateno para configurao desses valores conforme definidos no projeto.
   *
   * > Veja os parmetros customizveis na interface `ThfPageBlockedUserReasonParams`.
   *
   */
  @Input('t-params') set params(value: ThfPageBlockedUserReasonParams) {
    if (value instanceof Object) {
      const keys = Object.keys(value);
      const newParams = {...ThfPageBlockedUserParamsDefault};

      keys.forEach(key => {
        newParams[key] = value[key];
      });

      this._params = newParams;
    } else {
      this._params = {...ThfPageBlockedUserParamsDefault};
    }
  }

  get params() {
    return this._params;
  }

  /** Caminho para a logomarca localizada na parte superior, caso no seja definida ou seja invlida assume a logo padro da TOTVS. */
  @Input('t-logo') logo?: string;

  /**
   * @optional
   *
   * @description
   *
   * Definio de motivo de bloqueio de usurio. As informaes modificam conforme o motivo selecionado.
   *
   * > Veja os valores vlidos no *enum* `ThfPageBlockedUserReason`.
   *
   * @default `ThfPageBlockedUserReason.None`
   */
  @Input('t-reason') set reason(value: ThfPageBlockedUserReason) {
    this._reason = (<any>Object).values(ThfPageBlockedUserReason).includes(value) ? value : ThfPageBlockedUserReasonDefault;
  }

  get reason() {
    return this._reason;
  }

  /**
   * @optional
   *
   * @description
   *
   * Caminho para a logomarca localizada no rodap.
   */
  @Input('t-secondary-logo') secondaryLogo?: string;

  /**
   * @optional
   *
   * @description
   *
   * URL para a ao de retorno da pgina.
   *
   * @default `/`
   */
  @Input('t-url-back') set urlBack(url: string) {
    this._urlBack = url;
  }

  get urlBack() {
    return this._urlBack;
  }

}
