import { EventEmitter, Input, Output } from '@angular/core';

import { ThfBreadcrumb, ThfDynamicFormField, ThfPageAction } from '@totvs/thf-ui';

import { browserLanguage, thfLocaleDefault } from '../../utils/util';

export const thfPageDynamicSearchLiteralsDefault = {
  en: {
    disclaimerGroupTitle: 'Displaying results filtered by:',
    filterSettingsPlaceholder: 'Search',
    quickSearchLabel: 'Quick search:'
  },
  es: {
    disclaimerGroupTitle: 'Presentando resultados filtrados por:',
    filterSettingsPlaceholder: 'Buscar',
    quickSearchLabel: 'Bsqueda rpida:'
  },
  pt: {
    disclaimerGroupTitle: 'Apresentando resultados filtrados por:',
    filterSettingsPlaceholder: 'Pesquisar',
    quickSearchLabel: 'Pesquisa rpida:'
  }
};

/**
 * @description
 *
 * Componente com as aes de pesquisa j definidas, bastando que o desenvolvedor implemente apenas a chamada para as APIs
 * e exiba as informaes.
 */
export class ThfPageDynamicSearchBaseComponent {

  private _filters: Array<ThfDynamicFormField> = [];

  literals = {
    ...thfPageDynamicSearchLiteralsDefault[thfLocaleDefault],
    ...thfPageDynamicSearchLiteralsDefault[browserLanguage()]
  };

  /** Nesta propriedade deve ser definido um array de objetos que implementam a interface `ThfPageAction`. */
  @Input('t-actions') actions?: Array<ThfPageAction> = [];

  /** Objeto com propriedades do breadcrumb. */
  @Input('t-breadcrumb') breadcrumb?: ThfBreadcrumb = { items: [] };

  /**
   * @optional
   *
   * @description
   *
   * Lista dos campos usados na busca avanada. Caso o mesmo no seja passado a busca avanada no ser exibida.
   */
  @Input('t-filters') set filters(filters: Array<ThfDynamicFormField>) {
    this._filters = Array.isArray(filters) ? [...filters] : [];
  }

  get filters(): Array<ThfDynamicFormField> {
    return this._filters;
  }

  /** Ttulo da pgina. */
  @Input('t-title') title: string;

  /**
   * @description
   *
   * Evento disparado ao executar a pesquisa avanada, o mesmo ir repassar um objeto com os valores preenchidos no modal de pesquisa.
   *
   * > Campos no preenchidos no iro aparecer no objeto passado por parmetro.
   */
  @Output('t-advanced-search') advancedSearch?: EventEmitter<any> = new EventEmitter();

  /** Evento disparado ao remover um ou todos os disclaimers pelo usurio. */
  @Output('t-change-disclaimers') changeDisclaimers?: EventEmitter<any> = new EventEmitter();

  /** Evento disparado ao realizar uma busca pelo campo de pesquisa rpida, o mesmo ser chamado repassando o valor digitado. */
  @Output('t-quick-search') quickSearch?: EventEmitter<string> = new EventEmitter();

}
