import { EventEmitter, Input, Output } from '@angular/core';

import { ThfDynamicFormField } from '@totvs/thf-ui';

import { browserLanguage, thfLocaleDefault } from '../../../utils/util';

export const thfAdvancedFiltersLiteralsDefault = {
  en: {
    title: 'Advanced search',
    primaryActionLabel: 'Apply filters',
    secondaryActionLabel: 'Cancel'
  },
  es: {
    title: 'Bsqueda avanzada',
    primaryActionLabel: 'Aplicar filtros',
    secondaryActionLabel: 'Cancelar'
  },
  pt: {
    title: 'Busca avanada',
    primaryActionLabel: 'Aplicar filtros',
    secondaryActionLabel: 'Cancelar'
  }
};

/**
 * @docsPrivate
 *
 * @description
 *
 * Filtro de busca avanada criado a partir de um formulrio dinmico.
 * Componente de uso interno.
 */
export class ThfAdvancedFilterBaseComponent {

  private _filters: Array<ThfDynamicFormField> = [];

  literals = {
    ...thfAdvancedFiltersLiteralsDefault[thfLocaleDefault],
    ...thfAdvancedFiltersLiteralsDefault[browserLanguage()]
  };

  /**
   * Coleo de objetos que implementam a interface ThfDynamicFormField, para definio dos campos que sero criados
   * dinamicamente.
   */
  @Input('t-filters') set filters(filters: Array<ThfDynamicFormField>) {
    this._filters = Array.isArray(filters) ? [...filters] : [];
  }

  get filters() {
    return this._filters;
  }

  /** Funo que ser disparada e receber os valores do formulrio ao ser clicado no boto buscar. */
  @Output('t-search-event') searchEvent = new EventEmitter<any>();

}
